@import url(https://fonts.googleapis.com/css2?family=Kumar+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}
body{
  background-color: rgb(8, 8, 8);
}
#particalBachground{
  z-index: -1;
  position: fixed;
  width: 100%;
  height: 100%;
}
.IndianMap {
    height: 100px;
}
path{
    transition: 350ms ease-in-out;
}
path:active{
    border: none;
    outline: none;
}
path:focus{
    border: none;
    outline: none;
}
.Heading {
    font-family: 'Kumar One', cursive;
    font-weight: bolder;
    text-shadow: 2px 2px 0px rgb(121, 151, 76);
    font-size: xx-large;
    color: rgb(88, 16, 16);
}

.ButtonColumns {
    display: flex;
    flex-direction: column;
    margin-left: 0;
    left: 0;
    top: 50px;
    position: fixed;
    text-align: left;
}
.ButtonStyle {
    min-width: 400px;
    min-height: 100px;
    outline: none;
    border: none;
    margin-bottom: 10px;    
    margin-top: 50px;
    background-color: rgb(13, 12, 12);
    color: rgb(121, 151, 76);
    box-shadow: 5px 5px 0px rgb(121, 151, 76);
    text-align: center;
    margin: 10px;
    font-family: 'Kumar One', cursive;
}
.ButtonStyle:active{
    -webkit-transform: translate(2px ,2px);
            transform: translate(2px ,2px);
    box-shadow: 1px 1px 0px rgb(121, 151, 76);
}
.YearSlider {
    position: fixed;
    bottom: 0px;
    bottom: 100px;
    margin-left: 50px;
    height: 10vh;
    width: 40vw;
}
.track{
    height: 10vh;
    width: 40vw;
    background-color: transparent;
}
input[type=range]{
-webkit-appearance: none;
}

input[type=range]::-webkit-slider-runnable-track {
    background: rgb(119, 13, 13);
    border-radius: 5px;
}

input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
    border: none;
    height: 16px;
    width: 16px;
    border-radius: 50%;
    background: goldenrod;
    margin-top: -4px;
    box-shadow: 0px 0px 10px rgb(245, 202, 95);
}

input[type=range]:focus {
    outline: none;
}

input[type=range]:focus::-webkit-slider-runnable-track {
    background: rgb(119, 13, 13);
}

.currentYear{
    color: white;
    text-align: center;
    margin-left: 10px;
    font-size: x-large;
    font-family: 'Kumar One', cursive;
}
